*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: cursive;
}
html {
  font-size: 75%;
}
body{
  background-image: linear-gradient(#89d4cf,#734ae8
  );
}
.mdiv{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  
}
.cdiv{

  margin-top: 12rem;  
  text-align: center;
  
  
  
}
.cdiv h1{
  text-align: center;
  background-color: seagreen;
  border:3px solid yellowgreen;
  padding: 5px;
  -webkit-text-stroke-color:mediumaquamarine ;
  -webkit-text-stroke-width: 2px;
  letter-spacing: 5px;
  text-transform: uppercase;
  
}
.cdiv h1 i{
 background-color:blueviolet;
 text-align: center;
 padding: 10px;
 border-radius: 30px;
 
 
}
.cdiv h1 i:hover{
  background-color: plum;
}
.aitems{
  padding: 20px;
}
.showI{
  
  margin-top: 3rem;
  text-align: center;
}


.eitem {
  
  border-radius: 0.5rem;
  margin-bottom: 0.7rem;
  margin-left: 1rem;
  min-width: 40rem;
  
  letter-spacing: 2px;
  text-transform: capitalize;
  background-color: mediumaquamarine;
  justify-content: space-between;
  align-items: center;
  border:3px solid black;
}
.eitem h3{
  padding: 5px;
  font-size: 16px;
  text-align: center;
  
}


.eitem:hover {
  background-color: midnightblue;
  opacity:0.8;
  color: white;
  font-weight: bolder;
}


.eitem h3 i{
  color:white;
  background-color: black;
  padding: 7px;
  border-radius: 30px;
  margin-left: 20px;
  align-items: center;
  cursor: pointer;
}
.eitem h3 i:hover{
  background-color: red;
}
.eitem h3 i.fa-pen-to-square:hover{
  background-color: green;
}

.cdiv input[type=text]{
padding: 20px;
font-style: italic;
font-weight: bolder;
text-transform: capitalize;
text-align: center;
letter-spacing: 3px;
font-size: 18px;
border: 5px solid blue;
background-color: palegreen;

}
.cdiv button{
  margin-left: 20px;
  padding: 10px;
  border-radius: 50px;
  font-size: 18px;
  font-weight: bolder;
  background-color: lightblue;
}
.cdiv button:hover{
  background-color: indigo;
  opacity: 0.7;
}

